* {
  margin: 0;
  padding: 0; }

body {
  background-color: #22222e;
  color: #fff;
  font-family: "Raleway", sans-serif; }

.menu {
  text-align: center;
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -70%);
          transform: translate(-50%, -70%); }

.btn-wrap {
  margin-top: 10px; }

.btn-animated {
  -webkit-animation: moveInBottom .5s ease-in .5s;
          animation: moveInBottom .5s ease-in .5s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards; }

.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  color: black;
  letter-spacing: 1px;
  font-weight: bolder;
  padding: 10px 30px;
  display: inline-block;
  border-radius: 20px;
  -webkit-transition: all .2s;
  transition: all .2s;
  position: relative; }

.btn-white {
  background-color: #fff;
  color: #fff; }

.btn-light-blue {
  background-color: #00a3ee;
  color: #000; }

.btn-green {
  background-color: #00d873;
  color: #000; }

.btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 5px 10px #00000065; }

.btn:active {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px); }

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all .5s;
  transition: all .5s; }

.btn:hover::after {
  background-color: #fff;
  -webkit-transform: scale(1.5, 1.7);
          transform: scale(1.5, 1.7);
  opacity: 0; }

.btn-white::after {
  background-color: #fff; }

.btn-light-blue::after {
  background-color: #00a3ee; }

.btn-green::after {
  background-color: #00d873; }

.board {
  position: absolute;
  left: 50%;
  top: 50%;
  padding-top: 3px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 361px;
  height: 538px;
  border: 3px solid #fff;
  border-radius: 3px; }

.square {
  border-radius: 1px;
  margin: -2px 0px;
  box-sizing: border-box;
  border: .02px #fff solid;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.068);
  display: inline-block;
  height: 30px;
  width: 30px; }

.square-yellow {
  border: none;
  background-color: #f5f506; }

.square-red {
  border: none;
  background-color: #c23030; }

.square-blue {
  border: none;
  background-color: #0000e0; }

.square-green {
  border: none;
  background-color: #00d873; }

.square-cyan {
  border: none;
  background-color: #2ed8d8; }

.square-none {
  background-color: #2d2d3d; }

.score-board {
  text-align: center;
  position: absolute;
  left: 75%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.score-label {
  font-weight: bolder; }

.score-value {
  color: greenyellow;
  padding-bottom: 40px;
  padding-top: 5px; }

.help {
  position: absolute;
  left: 5%;
  top: 20%; }

.desc-item {
  padding-bottom: 10px; }

.description {
  font-style: italic; }

